.editors {
  width: 100%;
  flex-grow: 1;
  display: flex;
  justify-content: space-between;
  background-color: antiquewhite;
}

.editors > div {
  width: 50%;
}

.editors > .output.loading {
  filter: blur(3px);
}

.editors > .output .logo {
  width: 50px;
  height: 50px;
  position: absolute;
  z-index: 999;
  right: 45px;
  top: 45px;
}
